"use client";

import name from 'module';
import Link from "next/link";
import Image from "next/image";

import { useRouter } from "next/navigation";
import { useState } from "react";
function User() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [isSuccess, setIsSuccess] = useState(false)

  const router = useRouter();

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSuccess(false)

    if (!email.includes(".com")) {
      setMessage("Email must contain .com");
    } else if (password.length <= 8) {
      setMessage("Password must contain atleast 8 characters");
    } else {
      setMessage("Login in success");
      setIsSuccess(true)

      setTimeout(() => {
        router.push("/");
      }, 2000);
    }
  };
  return (
    <div className="login">
      <div className="login-area">
        
        <h5 className="text-geen">{message}</h5>


        <div className="login-content">
            <div className="login-form">
            <form onSubmit={handleSubmit}>
            <input
              type="email"
              value={email}
              placeholder="Enter Your Email"
              onChange={(e) => setEmail(e.target.value)}
              required
            /> <br/>
            <input
              type="password"
              value={password}
              placeholder="Your password"
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <button type="submit">Sign In</button>
          </form>
            </div>

            <div className="signUp">
          <h5>You do not have an account? </h5>
          <Link href="/signUp">Click here</Link>
            </div>

            <div className="help">
          <h4>
            For further support, you may visit the Help Center or contact our
            customer service team.
          </h4>
            </div>
        </div>
   
        <div className="img">
            <Image src="/login1.avif" alt="" width={100} height={100}/>
        </div>


      </div>
    </div>
  );
}
export default User;
